
import { Component, OnInit } from '@angular/core';

import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';

import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { DirektoriPegawaiPendidikanInformalService } from 'src/app/sdk/hr/services';
import { Pegawai_pendidikan_informal } from 'src/app/sdk/hr/models';



@Component({
    selector: 'app-add-informal',
    templateUrl: './add-informal.component.html',
    styleUrls: ['./add-informal.component.scss']
})
export class AddInformalComponent implements OnInit {

    constructor(
        private direktoriPegawaiPendidikanInformalService: DirektoriPegawaiPendidikanInformalService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pegawaiDetailService: PegawaiDetailService,
        private pesan: PesanService,
        private router: Router,
    ) { }

    id_pegawai: string = ''
    dataPendidikanInformal: Pegawai_pendidikan_informal

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.form()
    }

    dataForm: FormGroupTypeSafe<Pegawai_pendidikan_informal>
    form() {
        this.dataForm = this.fb.group<Pegawai_pendidikan_informal>({
            type: new FormControl('', [Validators.required]),
            nama_pendidikan: new FormControl(''),
            tanggal_mulai: new FormControl(''),
            tanggal_selesai: new FormControl(''),
            organizer: new FormControl(''),
            lokasi: new FormControl(''),
            remarks: new FormControl(''),
            id_pegawai: new FormControl(this.id_pegawai),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataPendidikanInformal = this.dataForm.value
        this.direktoriPegawaiPendidikanInformalService
            .postDirektoriPegawaiPendidikanInformals(this.dataPendidikanInformal)
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'biodata', 'pendidikan', 'list'])
    }
}
