import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { PendidikanRoutingModule } from './pendidikan-routing.module';
import { ListComponent } from './list/list.component';
import { AddComponent } from './add/add.component';
import { EditComponent } from './edit/edit.component';
import { NgZorroAntdModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { DetailComponent } from './detail/detail.component';
import { AddInformalComponent } from './add-informal/add-informal.component';
import { EditInformalComponent } from './edit-informal/edit-informal.component';


@NgModule({
    declarations: [ListComponent, AddComponent, EditComponent, DetailComponent, AddInformalComponent, EditInformalComponent],
    imports: [
        CommonModule,
        PendidikanRoutingModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule,
        PerfectScrollbarModule,
    ],
    providers:
        [
            FormBuilderTypeSafe
        ],
})
export class PendidikanModule { }
