import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { DirektoriPegawaiPendidikanService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Pegawai_pendidikan } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private direktoriPegawaiPendidikanService: DirektoriPegawaiPendidikanService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private activatedRoute: ActivatedRoute,
        private pegawaiDetailService: PegawaiDetailService,
        private pesan: PesanService,
        private router: Router,
    ) { }

    id_pegawai: string = ''
    dataPendidikan: Pegawai_pendidikan = {}

    id_pegawai_pendidikan: string = ''
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.activatedRoute.paramMap.subscribe(data => {
            if (data.get('id_pegawai_pendidikan')) {
                this.id_pegawai_pendidikan = data.get('id_pegawai_pendidikan')
                console.log(data.get('id_pegawai_pendidikan'))

                this.getData()
                this.form()
            }
        })
    }

    getData() {
        this.direktoriPegawaiPendidikanService.getDirektoriPegawaiPendidikansId(this.id_pegawai_pendidikan).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
            }
        )
    }

    dataForm: FormGroupTypeSafe<Pegawai_pendidikan>
    form() {
        this.dataForm = this.fb.group<Pegawai_pendidikan>({
            tingkat_pendidkan: new FormControl('', [Validators.required]),
            nama_sekolah: new FormControl(''),
            tanggal_masuk: new FormControl(''),
            tanggal_selesai: new FormControl(''),
            nilai: new FormControl(''),
            alamat_sekolah: new FormControl(''),
            fakultas: new FormControl(''),
            jurusan: new FormControl(''),
            id_pegawai: new FormControl(this.id_pegawai),
            id_dokumen: new FormControl(this.id_pegawai),
            no_dokumen: new FormControl(this.id_pegawai),
            id_user_create: new FormControl(this.id_pegawai),
            id_pegawai_pendidikan: new FormControl(this.id_pegawai),
            created_at: new FormControl(this.id_pegawai),
            updated_at: new FormControl(this.id_pegawai),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataPendidikan = this.dataForm.value
        this.direktoriPegawaiPendidikanService
            .putDirektoriPegawaiPendidikansId({id: this.id_pegawai_pendidikan, body: this.dataPendidikan})
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'biodata', 'pendidikan', 'list'])
    }

}
