import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarPendidikanService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PelamarServiceService } from '../../service/pelamar-service.service';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Pelamar_pendidikan } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private direktoriPelamarPendidikanService: DirektoriPelamarPendidikanService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pelamarServiceService: PelamarServiceService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) {}

    id_pelamar: string = ''
    dataPendidikan: Pelamar_pendidikan = {}

    id_pelamar_pendidikan: string = ''

    ngOnInit() {
        this.id_pelamar_pendidikan = this.activatedRoute.snapshot.paramMap.get('id_pelamar_pendidikan')
        this.pelamarServiceService.getIdPelamar().subscribe(data => {
            this.id_pelamar = data
            console.log('id pelamar paja tu ' + this.id_pelamar)
        })
        this.form()
        this.getData()
    }

    getData() {
        this.direktoriPelamarPendidikanService.getDirektoriPelamarPendidikansId(this.id_pelamar_pendidikan).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
            }
        )
    }

    dataForm: FormGroupTypeSafe<Pelamar_pendidikan>
    form() {
        this.dataForm = this.fb.group<Pelamar_pendidikan>({
            tingkat_pendidkan: new FormControl('', [Validators.required]),
            nama_sekolah: new FormControl(''),
            tanggal_masuk: new FormControl(''),
            tanggal_selesai: new FormControl(''),
            nilai: new FormControl(''),
            alamat_sekolah: new FormControl(''),
            fakultas: new FormControl(''),
            jurusan: new FormControl(''),
            id_dokumen: new FormControl(''),
            id_pelamar_pendidikan: new FormControl(''),
            id_user_create: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            no_dokumen: new FormControl(''),
            id_pelamar: new FormControl(this.id_pelamar),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataPendidikan = this.dataForm.value
        this.direktoriPelamarPendidikanService
            .putDirektoriPelamarPendidikansId({id: this.id_pelamar_pendidikan, body: this.dataPendidikan})
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/detail-pelamar', this.id_pelamar, 'pendidikan', 'list'])
    }

}
