import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd';

import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { DirektoriPegawaiPendidikanService, DirektoriPegawaiPendidikanInformalService } from 'src/app/sdk/hr/services';
import { Pegawai_pendidikan, Pegawai_pendidikan_informal } from 'src/app/sdk/hr/models';


@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriPegawaiPendidikanService: DirektoriPegawaiPendidikanService,
        private direktoriPegawaiPendidikanInformalService: DirektoriPegawaiPendidikanInformalService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService
    ) { }

    id_pegawai: string = ''
    dataPendidikan: Pegawai_pendidikan[] = []
    dataPendidikanInformal: Pegawai_pendidikan_informal[] = []
    id_pegawai_pendidikan: string = ''
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.getPendidikan()
        this.getPendidikanInformal()
    }


    getPendidikan() {
        this.direktoriPegawaiPendidikanService.getDirektoriPegawaiPendidikans(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.dataPendidikan = data.data
            }
        )
    }



    delete(id) {
        this.spinner.show()
        this.direktoriPegawaiPendidikanService
            .deleteDirektoriPegawaiPendidikansId(id)
            .subscribe(data => {
                this.getPendidikan()
                this.spinner.hide()
            })

    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }



    getPendidikanInformal() {
        this.direktoriPegawaiPendidikanInformalService.getDirektoriPegawaiPendidikanInformals(JSON.stringify({id_pegawai : this.id_pegawai})).subscribe(
            data=> {
                this.dataPendidikanInformal = data.data
            }
        )
    }

    deleteInformal(id) {
        this.spinner.show()
        this.direktoriPegawaiPendidikanInformalService
            .deleteDirektoriPegawaiPendidikanInformalsId(id)
            .subscribe(data => {
                this.getPendidikanInformal()
                this.spinner.hide()
            })

    }

}
