import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ListComponent } from './list/list.component';
import { AddComponent } from './add/add.component';
import { EditComponent } from './edit/edit.component';
import { EditInformalComponent } from './edit-informal/edit-informal.component';
import { AddInformalComponent } from './add-informal/add-informal.component';


const routes: Routes = [
    {
        path:'list',
        component:ListComponent
    },
    {
        path:'add',
        component:AddComponent
    },
    {
        path:'add-informal',
        component:AddInformalComponent
    },
    {
        path:'edit/:id_pelamar_pendidikan',
        component:EditComponent
    },
    {
        path:'edit-informal/:id_pendidikan_informal',
        component:EditInformalComponent
    }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PendidikanRoutingModule { }
