import { DirektoriPegawaiPendidikanService } from 'src/app/sdk/hr/services';
import { Pegawai_pendidikan } from 'src/app/sdk/hr/models';
import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { NzMessageService } from 'ng-zorro-antd';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private direktoriPegawaiPendidikanService: DirektoriPegawaiPendidikanService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService
      ) { }

      id_pegawai: string = ''
      id_pegawai_pendidikan: string = ''

      dataPendidikan: Pegawai_pendidikan = {}

        ngOnInit() {
            this.pegawaiDetailService.getIdPegawai().subscribe(data => {
                this.id_pegawai = data
                console.log('id pegawai paja tu ' + this.id_pegawai)
            })
            this.activatedRoute.paramMap.subscribe(data => {
                if (data.get('id_pegawai_pendidikan')) {
                    this.id_pegawai_pendidikan = data.get('id_pegawai_pendidikan')
                }
            })
            this.getPendidikan()
        }

        getPendidikan() {
            this.spinner.show()
            this.direktoriPegawaiPendidikanService.getDirektoriPegawaiPendidikansId(this.id_pegawai_pendidikan)
                .subscribe(data => {
                    this.dataPendidikan = data.data
                    this.spinner.hide()
                })
        }

}
