import { PelamarServiceService } from './../../service/pelamar-service.service';
import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarPendidikanService } from 'src/app/sdk/hr/services';
import { Pelamar_pendidikan } from 'src/app/sdk/hr/models';
import { Router } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { DirektoriPelamarPendidikanInformalService } from 'src/app/sdk/hr/services/direktori-pelamar-pendidikan-informal.service';
import { Pelamar_pendidikan_informal } from 'src/app/sdk/hr/models/pelamar-_pendidikan-_informal';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriPelamarPendidikanService: DirektoriPelamarPendidikanService,
        private direktoriPelamarPendidikanInformalService: DirektoriPelamarPendidikanInformalService,
        private router: Router,
        private pelamarServiceService: PelamarServiceService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
    ) {}

    dataPendidikan: Pelamar_pendidikan [] = []
    dataPendidikanInformal: Pelamar_pendidikan_informal [] = []

    id_pelamar: string = ''

    ngOnInit() {
        this.pelamarServiceService.getIdPelamar().subscribe(data => {
            this.id_pelamar = data
            console.log('id pelamar paja tu ' + this.id_pelamar)
            this.getPendidikan()
            this.getPendidikanInformal()
        })

    }

    getPendidikan() {
        this.direktoriPelamarPendidikanService.getDirektoriPelamarPendidikans(JSON.stringify({ id_pelamar: this.id_pelamar })).subscribe(
            data=> {
                this.dataPendidikan= data.data
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.direktoriPelamarPendidikanService
            .deleteDirektoriPelamarPendidikansId(id)
            .subscribe(data => {
                this.getPendidikan()
                this.spinner.hide()
            })
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }


    getPendidikanInformal() {
        this.direktoriPelamarPendidikanInformalService.getDirektoriPelamarPendidikanInformals(JSON.stringify({id_pelamar : this.id_pelamar})).subscribe(
            data=> {
                this.dataPendidikanInformal = data.data
            }
        )
    }

    deleteInformal(id) {
        this.spinner.show()
        this.direktoriPelamarPendidikanInformalService
            .deleteDirektoriPelamarPendidikanInformalsId(id)
            .subscribe(data => {
                this.getPendidikanInformal()
                this.spinner.hide()
            })

    }



}
