import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarPendidikanInformalService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Pelamar_pendidikan_informal } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { PelamarServiceService } from '../../service/pelamar-service.service';

@Component({
  selector: 'app-edit-informal',
  templateUrl: './edit-informal.component.html',
  styleUrls: ['./edit-informal.component.scss']
})
export class EditInformalComponent implements OnInit {

    constructor(
        private direktoriPelamarPendidikanInformalService: DirektoriPelamarPendidikanInformalService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pelamarServiceService: PelamarServiceService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) { }

    id_pelamar: string = ''
    id_pendidikan_informal: string = ''
    dataPendidikanInformal: Pelamar_pendidikan_informal

    ngOnInit() {
        this.id_pendidikan_informal = this.activatedRoute.snapshot.paramMap.get('id_pendidikan_informal')
        this.pelamarServiceService.getIdPelamar().subscribe(data => {
            this.id_pelamar = data
            console.log('id pegawai paja tu ' + this.id_pelamar)
        })
        this.form()
        this.getData()
    }

    getData() {
        this.direktoriPelamarPendidikanInformalService.getDirektoriPelamarPendidikanInformalsId(this.id_pendidikan_informal).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
            }
        )
    }

    dataForm: FormGroupTypeSafe<Pelamar_pendidikan_informal>
    form() {
        this.dataForm = this.fb.group<Pelamar_pendidikan_informal>({
            type: new FormControl('', [Validators.required]),
            nama_pendidikan: new FormControl(''),
            tanggal_mulai: new FormControl(''),
            tanggal_selesai: new FormControl(''),
            organizer: new FormControl(''),
            lokasi: new FormControl(''),
            remarks: new FormControl(''),
            id_pendidikan_informal: new FormControl(''),
            id_user: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            id_pelamar: new FormControl(this.id_pelamar),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataPendidikanInformal = this.dataForm.value
        this.direktoriPelamarPendidikanInformalService
            .putDirektoriPelamarPendidikanInformalsId({id: this.id_pendidikan_informal, body: this.dataPendidikanInformal})
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/detail-pelamar', this.id_pelamar, 'pendidikan', 'list'])
    }

}
